'use client';

interface ConfirmModalProps {
    isOpen: boolean;
    userName: string;
    title?: string;
    message?: string;
    onConfirm: () => void;
    onCancel: () => void;
}

export default function ConfirmModal(props: ConfirmModalProps) {
    const {
        isOpen,
        userName,
        title = 'Confirm Action',
        message = `Are you sure you want to proceed with this action?`,
        onConfirm,
        onCancel,
    } = props;

    if (!isOpen) return null;

    return (
        <div className="fixed inset-0 bg-black/85 flex items-center justify-center z-50">
            <div className="bg-zinc-900 rounded-lg p-6 max-w-sm w-full mx-4 shadow-2xl border border-zinc-700">
                <h3 className="text-lg font-semibold text-white mb-2">{title}</h3>
                <p className="text-gray-400 text-sm mb-4">
                    {message}
                    <br />
                    <span className="font-semibold text-white">{userName}</span>
                </p>

                <div className="flex gap-3 justify-end">
                    <button
                        onClick={onCancel}
                        className="px-4 py-2 rounded bg-zinc-800 border border-zinc-700 text-sm text-gray-300 hover:bg-zinc-700"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={onConfirm}
                        className="px-4 py-2 rounded bg-red-600 text-sm text-white hover:bg-red-700"
                    >
                        Delete
                    </button>
                </div>
            </div>
        </div>
    );
}